@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@900&family=Roboto&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #f5f5f5;
  color: #444;
  height: 100vh;
}

.todos {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1,
h2 {
  font-family: "Noto Sans JP", sans-serif;
}

.heading {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 1em 0 0.5em;
}

.heading h1 {
  font-size: 2.3em;
}

#logo {
  height: 2.5em;
  margin-left: 0.7rem;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}

.wrapper {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  width: 90%;
  max-width: 400px;
}

.delete-completed {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: none;
  background: none;
  padding: 0.5rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  color: #444;
  cursor: pointer;
  margin-bottom: 1rem;
  background-color: #dfdfdf;
  border-radius: 10px;
}

.delete-completed:hover {
  background-color: #c9c9c9;
}

.delete-completed-svg {
  height: 32px;
  width: 32px;
  margin-right: 0.5rem;
}

.delete-completed:disabled .delete-completed-svg circle {
  fill: #ec7575;
}

.delete-completed:disabled {
  cursor: not-allowed;
  background-color: #f5f5f5;
}

.input {
  border: none;
  font-size: 1.2em;
  padding: 1em;
  display: block;
  width: 100%;
}

.todos li {
  list-style: none;
  align-self: flex-start;
  border-top: 1px solid #e5e5e5;
  cursor: pointer;
  font-size: 1.3em;
  padding: 1em;
}

.todos li.completed {
  color: #b6b6b6;
  text-decoration: line-through;
}

small {
  font-size: 0.7em;
  margin: 2em 0;
  text-align: center;
}

/* MEDIA QUERIES */

@media screen and (min-width: 375px) {
  .heading h1 {
    font-size: 3em;
  }

  .wrapper {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }

  #logo {
    height: 3em;
    margin-left: 1em;
  }

  .delete-completed {
    font-size: 1.25rem;
  }

  .input {
    font-size: 1.7rem;
    padding: 1rem 2rem;
  }

  .todos li {
    border-top: 1px solid #e5e5e5;
    font-size: 1.5rem;
    padding: 1rem 2rem;
  }

  small {
    margin-top: 2rem;
    text-align: center;
  }
}

@media screen and (min-width: 768px) {
  .heading h1 {
    font-size: 3.5em;
  }

  #logo {
    height: 4em;
    margin-left: 1em;
  }

  .delete-completed {
    margin-bottom: 1.5rem;
    font-size: 1.5rem;
  }

  small {
    font-size: 0.9em;
  }
}

@media screen and (min-width: 1024px) {
  .heading h1 {
    font-size: 4em;
  }

  small {
    font-size: 1em;
  }
}
